@import url(https://fonts.googleapis.com/css2?family=Revalia&display=swap);
body {
  margin: 0;
  padding:0;
  background-color: rgb(23, 23, 23);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container {
    position: absolute;
    bottom: 0;
    margin: 1.5em;
}

.contact {
    color: white;
    padding: 0.5em;
}
.slideContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(63, 6, 50) 75%);
}
.slideGrid {
    font-family: "Verdana";
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    justify-items: center;
}
.slide {
    grid-row: 2/3;
}
.slideNav {
    position: relative;
    text-align: center;
}

.text {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    font-size: 250%;
    color: white;
}

.letter, .letterContainer {
    display: inline-block;
    padding: 0.1em;
}

.space {
    padding-right: 0.5em;
}
* {
    overflow: hidden;
}
